<template>
  <container :title="title">
    <list :editRoute="groupRoute" :show-del="true">
      <thead slot="thead">
      <tr>
        <th><checkbox v-model="checked" :checked="tgCheck"/></th>
        <th>{{lang.id}}</th>
        <th>{{lang.name}}</th>
        <th>{{lang.module}}</th>
        <th>{{lang.level}}</th>
        <th>{{lang.description}}</th>
        <th>{{lang.status}}</th>
        <th width="205">{{$t('operation.name')}}</th>
      </tr>
      </thead>
      <tbody slot="tbody">
      <tr v-for="(vol,index) in list">
        <td><checkbox  v-model="checkbox" :checked="checked" :value="vol.id"/></td>
        <td>{{vol.id}}</td>
        <td>{{vol.name}}</td>
        <td>{{vol.module}}</td>
        <td>{{vol.level}}</td>
        <td>{{vol.description}}</td>
        <v-td type="status" :value="vol.status" />
        <v-td type="opt" :value="vol.id"
              edit-route="auth_group_edit" />
      </tr>
      </tbody>
    </list>
  </container>
</template>

<script>
  import {Container,List, VTd, Checkbox} from "@/components"

  export default {
    components: {VTd, List, Container,Checkbox},
    data(){
      return{
        title:this.$t('auth_group.index'),
        lang:this.$t('auth_group'),
        checked:false,
        tgCheck:false,
        groupRoute:{
          name:'auth_group_edit'
        }
      }
    },
    watch:{
      checkbox(newValue){
        const length = newValue.length
        if (length === this.list.length){
          this.tgCheck = true
        }
        if (length === 0) {
          this.tgCheck = false
        }
      }
    },
    computed:{
      list(){
        return this.$store.state.table.list
      },
      checkbox:{
        get(){
          return this.$store.state.table.checkbox
        },
        set(value){
          this.$store.commit('table/SET_CHECKBOX',value)
        }
      }
    },
    mounted(){
      const url = '/api/user/group/index'
      this.$store.commit('table/SET_URL', url)
      this.$store.dispatch('table/fetchList')
    }
  }
</script>



